import wx from 'wx-sdk-ts';
import axios from "axios"
let wxShareModel: any = {
  getShareConfig:function () {
    axios({
      url: 'http://wcx.free.idcfengye.com/api/redEnvelope/getMsfWechatConfig',
      method: 'POST',
      params: {
        url: window.location.href
      }
    }).then((res:any) => {
      console.log(res);
      var data = res.data;
      wx.config({
        debug : true, // 本地微信开发者工具debug调试
        appId : data.appId,
        timestamp : data.timestamp,
        nonceStr : data.noncestr,
        signature : data.signature,
        jsApiList : [
          "onMenuShareTimeline",
          "onMenuShareAppMessage",
          "onMenuShareQQ",
          "onMenuShareWeibo",
          "onMenuShareQZone"]
      });
    });
    return this;
  },

  setShareConfig:function (wxConfig:any) {
    wx.ready(function(){
      wx.checkJsApi({
        jsApiList: [
          "onMenuShareTimeline",
          "onMenuShareAppMessage",
          "onMenuShareQQ",
          "onMenuShareWeibo",
          "onMenuShareQZone"
        ],
        success: function (res:any) {
          console.log(JSON.stringify(res));
        }
      });
      // 分享到朋友圈
      wx.onMenuShareTimeline({
        title : wxConfig.title,
        link : wxConfig.link,
        imgUrl : wxConfig.imgUrl,
        success: function(){
        }
      });
      // 分享给朋友
      wx.onMenuShareAppMessage({
        title : wxConfig.title,
        desc : wxConfig.desc,
        link : wxConfig.link,
        imgUrl : wxConfig.imgUrl,
        success: function(){
        }
      });
      // 分享到QQ
      wx.onMenuShareQQ({
        title : wxConfig.title,
        desc : wxConfig.desc,
        link : wxConfig.link,
        imgUrl : wxConfig.imgUrl
      });
      // 分享到腾讯微博
      wx.onMenuShareWeibo({
        title : wxConfig.title,
        desc : wxConfig.desc,
        link : wxConfig.link,
        imgUrl : wxConfig.imgUrl
      });
      // 分享到QQ空间
      wx.onMenuShareQZone({
        title : wxConfig.title,
        desc : wxConfig.desc,
        link : wxConfig.link,
        imgUrl : wxConfig.imgUrl
      });
      wx.error(function(res) {
      });
    });
  }
};
export const wxShare = wxShareModel.getShareConfig().setShareConfig;